* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
body {
  background-color: #000000;
  color: #fff;
}

.header__container {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 20px;
  height: 70px;
  z-index: 1;
  transition-timing-function: ease-in;
  transition: all 0.5s;
}
.nav__black {
  background-color: #000000;
}
.header__img {
  position: fixed;
  object-fit: contain;
}
.logo {
  left: 20px;
  width: 80px;
}
.avatar {
  right: 20px;
  width: 30px;
}
.isLarge {
  max-height: 250px;
}
.box-shadow {
  background-color: rgba(0, 0, 0, 0.75);
  border-radius: 4px;
  box-sizing: border-box;
  min-height: 515px;
  width: 30%;
  padding: 60px 55px;
  margin: 0 auto;
}
